





























import Vue from 'vue';
import api from '@/api';
import { mapState } from 'vuex';
import Spinner from '@/components/Spinner.vue';

export default Vue.extend({
  name: 'Details',
  components: {
    Spinner,
  },
  data() {
    return {
      entry: {},
    };
  },
  created() {
    api.fetchDetails(this.$route.params.id).then((data: object) => {
      this.entry = data;
      this.$store.commit('SET_LOADING', false);
    });
  },
  computed: {
    ...mapState(['loading']),
  },
});
